:root {
  --calculated-value: 100;
  --gapW: 6;
  --gapH: 2;
  --half: 50%;
  --Width: 900px;
  --Height: calc(var(--Width) / 6);
}

.wrapper {
  top: 100px;
  width: 100vw;
  height: 100vh;
  display: flex;
  justify-content: center;
  position: absolute;

  .video {
    height: 300px;
    width: var(--Width);
    overflow: hidden;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-left: 15px;
    clip-path: var(--clip-path);
    z-index: 20;
  }
  .wrapText {
    position: relative;
    width: var(--Height);
    height: var(--Height);
    justify-content: center;
    display: flex !important;
    text-align: left;
    align-items: last baseline;
  }
}
.wrapper1 {
  --clip-path: xywh(
    0 0 calc(var(--calculated-value) / var(--gapW) * 1%) calc(var(--half)) round
      20px 20px 20px 20px
  );

  animation: movewrapper1 5s infinite;
  transition: all ease-in-out 5s;
}
.wrapper2 {
  --clip-path: xywh(
    calc(var(--calculated-value) / var(--gapW) * 1%) 0
      calc(var(--calculated-value) / var(--gapW) * 1%) calc(var(--half)) round
      20px 20px 20px 20px
